      >{{item}}</li>
    </ul>
    <div class="img-items">
      <template v-if="activeItem=='All'">
        <transition name="fade" appear>
          <div class="img-item">
            <a href="https://ntsoccer.github.io/bootstrap/">
              <img
                src="@/assets/FireShot Capture 033 - Nuno - Responsive Bootstrap Theme - .png"
                alt
                width="100%"
              />
            </a>
          </div>
        </transition>
      </template>
      <template v-if="activeItem=='All' || activeItem=='JavaScript'">
        <transition name="fade" appear mode="out-in">
          <div class="img-item">
            <a href="img/FireShot Capture 028 - Takuya ポートフォリオ - .png">
              <img src="@/assets/FireShot Capture 031 - Takuya ポートフォリオ - .png" alt width="100%" />
            </a>
          </div>
        </transition>
        <transition name="fade" appear mode="out-in">
          <div class="img-item">
            <a href="https://ntsoccer.github.io/work_1/">
              <img src="@/assets/FireShot Capture 032 - work - .png" alt width="100%" />
            </a>
          </div>
        </transition>
        <transition name="fade" appear mode="out-in">
          <div class="img-item">
            <a href="https://ntsoccer.github.io/recruitment-responsive/">
              <img
                src="@/assets/FireShot Capture 039 - Takuya Nishimura - ntsoccer.github.io.png"
                alt
                width="100%"
              />
            </a>
          </div>
        </transition>
      </template>
      <template v-if="activeItem=='Python'">
        <transition name="fade" appear mode="out-in">     
          <p>{{message}}</p>
        </transition>
      </template>
    </div>
  </div>
</template>


<script>
export default {
  data: function() {
    return {
      title:"一覧",
      items: ["All", "JavaScript", "Python"],
      activeItem: "All",
      message: "これから勉強してポートフォリオが完成次第随時あげていきます！"
    };
  }
};
</script>

<style scoped>
.list {
  margin: 0 auto;
  display: flex;
  width: 350px;
  margin-top: 50px;
  justify-content: space-between;
}
.list-item {
  padding: 10px 0 0 0;
  list-style: none;
  flex-basis: 30%;
  height: 30px;
  border: solid 1px black;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0px 2px 0px 0;
  transition: transform 0.5s;
}
.list-item.selected {
  background: radial-gradient(#f2b9a1, #ea6264);
  box-shadow: none;
  transform: translate(0px, 2px);
}
.img-items {
  width: 1000px;
  margin: 0 auto;
  margin-top: 50px;
  justify-content: space-between;
  display: flex;
  flex-wrap: wrap;
}
.img-item {
  margin-top: 50px;
}
.img-item img {
  width: 450px;
}
.fade-enter-active {
  animation: fade-in 0.7s;
}
.fade-leave-active {
  animation: fade-in 0.7s reverse;
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
p {
  margin: auto;
  font-weight: 700;
}
@media screen and (max-width: 1024px){
  .img-items{
    width: 100%;
    flex-direction: column;
  }
  .img-item img{
    width: 90%;
    margin: 0 auto;
  }

.list{
  padding-left: 0px;
}
}

</style>